































export default{
	data(){
		return{
			phone:'',
			verificationCode:'',
			account:"",
			password:'',
			invitationCode:''
		}
	},
	methods:{
	// 获取手机验证码
		getPhone(){
			//缺少接口
		},
	// 注册
		register(){
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if(this.verificationCode ==''){
				uni.showToast({
					icon: 'none',
					title: '请输入短信验证码'
				});
				return;
			}
			const data ={
				phone:this.phone,
				verificationCode:this.verificationCode,
				account:this.account,
				password:this.password,
				invitationCode:this.invitationCode
			}
			uni.request({
				url:'http://10.138.93.113:2221/ScreenTheWord/MainController.do',
				method:'POST',
				header: {
					'Access-Control-Allow-Origin':'*' 
				},
				data:{
					controllerRequestType:'loginControllerService',
					jsonString:{
						'requestType':"registUserInfo",
						 userInfo:data
					}	
				},
				
				success: (res) => {
					// if();
					uni.showToast({
						title: '注册成功'
					});
				},
				fail:(response) =>{
					
				}
			})
		}
	}
}



